html {
	body {
		padding: 200px 0 0 0;
		nav {
			display: flex;
			justify-content: center;
			align-items: center;
			z-index: 100;
			position: fixed;
			top: 0;
			width: 100%;
			height: 100px;
			transition: 0.8s;
			background-color: rgb(0, 0, 0);
			&.active{
				>a{color: black;}
				height: 150px;
				background-color: transparent;
			}
			>a {
				color: white;
				margin: 10px 20px 10px 20px;
				border-radius: 11px;
				padding: 20px;
				font-size: 2rem;
				transition: 0.8s;
				&.active {
					color: red;
				}
			}
		}
	}
}

@media (max-width: 1000px) {
}

@media (max-width: 800px) {
	html{font-size: 12px;}
}

@media (max-width: 380px) {
	html{font-size: 8px;}
}

@media (max-width: 300px) {
	html{
		font-size: 8px;
		body{
			nav{
				justify-content: center;
				>a{
					padding: 3%;
				}
			}	
		}
	}
}
